import React from "react";
import { Link } from "react-router-dom";
import "./Policies.scss"; 
function Guidelines() {
  return (

    <div className="Pcontent"> 
      <style jsx>{`
                .goog-te-combo{ 
                    font-size:2rem;
                }
                #google_translate_element{ 
                    position:absolute;
                    right:0
                }
                `}</style>
      <h1>Frndsmeet Community Guidelines</h1>
      <p>
        <strong>Last Updated: 16-12-2021</strong>
      </p>
      <p>
        Frndsmeet was created to provide a platform where people could connect
        with other people of diverse backgrounds and experiences that may differ
        from their own. Frndsmeet’s Community Guidelines are intended to further
        that goal by providing general guidance and explaining what is and isn’t
        allowed on Frndsmeet. They are not intended to comprehensively identify
        every type of inappropriate or unlawful conduct or content. Users should
        be guided by common sense and an expectation that they should treat
        their fellow users with respect.
      </p>
      <p>
        These Community Guidelines apply to your access to and use of the
        Frndsmeet website and any services offered by Frndsmeet (collectively,
        the “<strong>Services</strong>”). Frndsmeet reserves the right to revise
        these Community Guidelines at any time for any reason and updates will
        be posted to the Services. These Community Guidelines are in addition to
        the Frndsmeet <Link to="/Terms">Terms of Service</Link> , which also
        apply to your access to and use of the Services.
      </p>
      <p>
        Frndsmeet reserves the right to ban (temporarily or permanently) or take
        other appropriate action with or without notice with respect to any user
        that engages in conduct that Frndsmeet determines, in its sole
        discretion, to be inappropriate or harmful, whether or not such conduct
        is identified below.
      </p>
      <p>
        If you encounter conduct or content on the Services that you believe
        violate any of the guidelines below or if you have other safety concerns
        about the Services, please contact Frndsmeet at [support@frndsmeet.com]
        and include “Frndsmeet-Safety” in the subject line. While Frndsmeet is
        not responsible to you as a user for enforcing these Community
        Guidelines, reports of violations are helpful to Frndsmeet.
      </p>
      <p>
        Parental control protections (such as computer hardware, software, or
        filtering services) are also commercially available and may assist you
        in limiting minors’ access to materials that may be harmful to or
        inappropriate for minors. There are a number of websites that provide
        information about such parental control protections, including but not
        limited to{" "}
        <a href="https://www.connectsafely.org/controls/">
          https://www.connectsafely.org/controls/
        </a>
      </p>
      <ul>
        <li>
          <strong>Violations of the Law.</strong> You must comply with all
          applicable local, national and international laws while using the
          Services. Any content, activity or conduct featuring, encouraging,
          offering, or soliciting illegal activity or content is prohibited.
          Frndsmeet reserves the right to report any such violations to law
          enforcement.
        </li>
        <li>
          <strong>Ban Circumvention.</strong> Any attempt to circumvent a ban,
          whether temporary or permanent, is prohibited.
        </li>
        <li>
          <strong>Violence and Threats.</strong> Threats of violence or threats
          to harm others are strictly prohibited. This includes threats to
          physically harm others, attempts to intimidate others, terroristic
          threats, threats to obtain or disclose personal information or private
          content of others or threats intended to force another user to engage
          in unwanted or inappropriate conduct.
        </li>
        <li>
          <strong>Hateful Conduct and Harassment.</strong> Conduct and content
          that attacks or denigrates others based on characteristics such as
          race, ethnicity, gender, sexual orientation, gender identity,
          immigration status, disability or national origin is prohibited.
          Harassment of any kind, including without limitation unwelcome sexual
          advances and solicitations, malicious false reporting of other users,
          and personal attacks, are prohibited.
        </li>
        <li>
          <strong>
            Nudity, Pornography and Sexually Explicit Conduct and Content.
          </strong>{" "}
          Nudity, pornography and sexually explicit conduct and content are
          prohibited on the moderated sections of the Services. Content or
          conduct that threatens or promotes sexual violence or exploitation are
          strictly prohibited on any portion of the Services and may be reported
          to law enforcement.
        </li>
        <li>
          <strong>Conduct or Content Involving Minors.</strong> All conduct or
          content that exploits, sexualizes or endangers the safety of minors in
          any way is strictly prohibited on any of the Services. We report such
          content and conduct to the National Center for Missing and Exploited
          Children and/or the appropriate law enforcement agency(ies).
        </li>
        <li>
          <strong>Unauthorized Sharing of Private Information.</strong> Respect
          the privacy of others. Do not attempt to obtain or share content that
          may reveal private personal information about individuals without
          their permission.
        </li>
        <li>
          <strong>Impersonation.</strong> Impersonating someone else or
          attempting to deceive other users about who you are (including by
          providing false information) is prohibited.
        </li>
        <li>
          <strong>Self-Harm.</strong> Conduct or content that glorifies,
          encourages or promotes self-harm is prohibited.
        </li>
        <li>
          <strong>Animal Cruelty.</strong> Conduct or content that involves or
          promotes harm or cruelty to animals is prohibited.
        </li>
        <li>
          <strong>Marketing and Advertising.</strong> The Services may not be
          used to market, advertise or promote any goods or services.
        </li>
        <li>
          <strong>Bots.</strong> Use of bots of any kind to access or use the
          Services is prohibited.
        </li>
      </ul>
    </div>
  );
}

export default Guidelines;
